const localItem = function (key, value) {
    if (arguments.length == 1) {
        return localStorage.getItem(key);
    } else {
        return localStorage.setItem(key, value);
    }
};

const removeLocalItem = function (key) {
    if (key) {
        return localStorage.removeItem(key);
    }
    return localStorage.removeItem();
};
//
const returnTop = (con)=> {
    if (localItem('scrollPosition')) {
        //
        if (!con.refs.lv) return;
        try {
            con.refs.lv.refs.listview.refs.listviewscroll.refs.ScrollView.scrollTop = localItem('scrollPosition');
        }
        catch (e) {
            console.log(e)
        }
        removeLocalItem('scrollPosition');
    }
};
let index = data.length - 1;

const NUM_ROWS = 20;
let pageIndex = 0;

class LanLodingBody extends Component {
    constructor(props) {
        super(props);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
        });

        this.genData = (pIndex = 0) => {
            const dataBlob = {};
            for (let i = 0; i < NUM_ROWS; i++) {
                const ii = (pIndex * NUM_ROWS) + i;
                dataBlob[`${ii}`] = `row - ${ii}`;
            }
            return dataBlob;
        };

        this.state = {
            dataSource: dataSource.cloneWithRows({}),
            isLoading: true,
        };
    }

    componentDidMount() {
        //this.onScrollPosition();
        localItem('scrollPosition', 2500);
        //localItem('scrollPosition', this.refs.lv.refs.listview.scrollProperties.offset);
         //this.refs.lv.refs.listview.scrollTo(10,0);
        //returnTop(this)
        //console.log(localItem('scrollPosition', this.refs.lv.refs.listview.scrollProperties.offset))

        // simulate initial Ajax

        setTimeout(() => {
            this.rData = this.genData();
            this.setState({
                dataSource: this.state.dataSource.cloneWithRows(this.rData),
                isLoading: false,
            });
        }, 600);
    }
    componentWillUnmount() {
        //alert('1')
        localItem('scrollPosition', this.refs.lv.refs.listview.scrollProperties.offset);
        console.log(localItem('scrollPosition'))
    }
    /*shouldComponentUpdate(nextProps, nextState) {
        return (this.props.homeListObj.homeDataList.length !=nextProps.homeListObj.homeDataList.length)||(this.props.homeListObj.articleType != nextProps.homeListObj.articleType )|| (this.props.homeListObj.pageIndex != nextProps.homeListObj.pageIndex);
    }*/
    // If you use redux, the data maybe at props, you need use `componentWillReceiveProps`
     componentWillReceiveProps(nextProps) {
       if (nextProps.dataSource !== this.props.dataSource) {
         this.setState({
           dataSource: this.state.dataSource.cloneWithRows(nextProps.dataSource),
         });
       }
     }
     //
    toRegister(){
        Utils.switchRoute('/register')
    }
    onEndReached (event){
        // load new data
        // hasMore: from backend data, indicates whether it is the last page, here is false
        /*if (this.state.isLoading && !this.state.hasMore) {
            return;
        }*/
        console.log('')
    }
    //
    onScrollPosition(){
        localItem('scrollPosition',2000)
    }
    render() {
        const separator = (sectionID, rowID) => (
            <div key={`${sectionID}-${rowID}`} style={{
                backgroundColor: '#F5F5F9',
                height: 8,
                borderTop: '1px solid #ECECED',
                borderBottom: '1px solid #ECECED',
            }}
            />
        );
        const row = (rowData, sectionID, rowID) => {
            if (index < 0) {
                index = data.length - 1;
            }
            const obj = data[index--];
            return (
                <div key={rowID} className="row" onClick={()=>{
                    console.log(rowID);
                    console.log("scroll:"+localItem('scrollPosition'))
                }}>
                    <div className="row-title">{obj.title}</div>
                    <div style={{ display: '-webkit-box', display: 'flex', padding: '0.3rem 0' }}>
                        <img style={{ height: '1.28rem', marginRight: '0.3rem' }} src={obj.img} />
                        <div className="row-text">
                            <div style={{ marginBottom: '0.16rem', fontWeight: 'bold' }}>{obj.des}</div>
                            <div><span onTouchStart={this.toRegister} style={{ fontSize: '0.6rem', color: '#FF6E27' }}>{rowID}</span>/</div>
                        </div>
                    </div>
                </div>
            );
        };
        return (
            <ListView ref="lv"
                      dataSource={this.state.dataSource}
                      renderHeader={() => <span>header</span>}
                      renderFooter={() => <div style={{ padding: 30, textAlign: 'center' }}>
                          {this.state.isLoading ? '...' : ''}
                      </div>}
                      renderRow={row}
                      renderSeparator={separator}
                      className="am-list"
                      pageSize={4}
                      scrollRenderAheadDistance={500}
                      scrollEventThrottle={20}
                      onScroll={() => {
                          localItem('scrollPosition', this.refs.lv.refs.listview.scrollProperties.offset);
                          //console.log(localItem('scrollPosition'));

                        }
                      }
                      useBodyScroll
                      onEndReached={this.onEndReached}
                      onEndReachedThreshold={10}
            />
        );
    }
}

