// start
import './projectList.scss'
// end

import React,{Component, propTypes } from "react" ;

import {Router , Route, Link , hashHistory} from "react-router";

//
import "./projectList.scss";
//
class ProjectList extends Component {
    constructor(){
        super()
    }

    componentDidMount(){
        //  start
        componentStore.set(this);
        //  end
    };
    componentWillUnmount(){
        //  start
        componentStore.clear(this);
        //  end
    };
    render(){
        return (

            <div className="projectList">
                <div className="listTop">
                    <img src={this.props.projectImg} alt=""/>
                </div>
                <div className="listBottom">
                    <h5>{this.props.projectTitle}</h5>
                    <div className="projectTime">
                        <div className="clearfix">
                            <div className="timeList">
                                <ul className="clearfix timeMath">
                                    <li className="positionR">
                                        <span className="spanFontA">{this.props.income}</span><span className="spanFontC">%</span>
                                        <div style={{display:this.props.staten}}>
                                            <div className="floatRate">
                                                <img src={this.props.floatRate} alt=""/>
                                            </div>
                                        </div>
                                    </li>
                                    <li><span className="spanFontB">{this.props.time}</span><span className="spanFontC"></span></li>
                                    <li><span className="spanFontB">{this.props.wholeMoney}</span><span className="spanFontC"></span></li>
                                </ul>
                                <ul className="clearfix timeTitle">
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                </ul>
                            </div>
                            <div className="btnInstantly" style={{display:this.props.stateBtn}} onTouchStart={function(){alert("")}}></div>
                            <div className="btnTime" style={{display:this.props.countDownBtn}} onTouchStart={function(){alert("")}}>
                                <div className="btnLength">25:36:55</div>
                            </div>

                        </div>
                        <progress max="100" value={this.props.progressValue}></progress>
                        <div>
                            <ul className="clearfix  moneyProgress">
                                <li className="done">{this.props.moneyProgress}</li>
                                <li className="liFlt">{this.props.differ}</li>
                            </ul>
                        </div>
                    </div>
                </div>

                <div style={{display:this.props.giftState}}>
                    <div className="gift">
                        <span><img src={require("../../../image/gift.png")} alt=""/></span><span>1000 {this.props.giftName}</span>
                    </div>
                </div>

            </div>
        )
    }
}
export default ProjectList;


